import {Component, Host, h, Prop, State, Watch} from '@stencil/core';

@Component({
  tag: 'soul-avatar-group',
  styleUrl: 'soul-avatar-group.scss',
  shadow: true,
})
export class SoulAvatarGroup {

  @Prop({reflect: true}) maxVisibleAvatars = 4;
  @Prop({reflect: true}) size: 's' | 'm' | 'l' | 'xl' = 'm';

  @State()
  private counter: CounterInfo = {visible: false};
  private slot: HTMLSlotElement;
  private supportedSizes = ['s', 'm', 'l', 'xl'];
  private VISIBLE_AVATAR_CLASS: AvatarClass = 'soul-avatar-group__visible-avatar';
  private HIDDEN_AVATAR_CLASS: AvatarClass = 'soul-avatar-group__hidden-avatar';


  @Watch('maxVisibleAvatars')
  public maxVisibleAvatarsChanged(): void {
    this.updateAvatarGroup();
  }

  @Watch('size')
  public sizeChange(newValue: string): void {
    this.validateSize(newValue);
    this.updateAvatarGroup();
  }

  render() {
    return (
      <Host>
          <slot onSlotchange={() => this.updateAvatarGroup()} ref={el => this.slot = el as HTMLSlotElement}></slot>
          {this.counter.visible ?
            <soul-avatar
              class={this.VISIBLE_AVATAR_CLASS}
              color="neutral"
              chars={'+' + this.counter.number}
              size={this.size}
              type="user">
            </soul-avatar> : ''}
      </Host>
    );
  }

  private updateAvatarGroup(): void {
    this.updateAvatarItems();
    this.updateCounter();
  }

  private updateCounter(): void {
    const currentCounter: CounterInfo = {
      visible: false
    };
    if (this.slot.assignedElements().length > this.maxVisibleAvatars) {
      currentCounter.visible = true;
      currentCounter.number = this.slot.assignedElements().length - this.maxVisibleAvatars
    }
    this.counter = currentCounter;
  }

  private updateAvatarItems(): void {
    this.slot.assignedElements().forEach((slotted: HTMLElement, index) => {
      if (index >= this.maxVisibleAvatars) {
        this.setAvatarVisibility(slotted, this.HIDDEN_AVATAR_CLASS);
      } else {
        this.setAvatarVisibility(slotted, this.VISIBLE_AVATAR_CLASS);
      }
      slotted.matches('soul-avatar') ?
        this.standardizeAvatar(slotted as HTMLSoulAvatarElement) :
        slotted.querySelectorAll('soul-avatar').forEach(soulAvatar => {
          this.standardizeAvatar(soulAvatar)
        });
    });
  }


  private validateSize(newValue: string): void {
    if (!this.supportedSizes.includes(newValue)) {
      console.debug(`Size ${newValue} not supported. Supported sizes: ${this.supportedSizes.join(', ')}`);
    }
  }

  private setAvatarVisibility(slotted: HTMLElement, avatarClass: string) {
    slotted.classList.remove(this.VISIBLE_AVATAR_CLASS, this.HIDDEN_AVATAR_CLASS);
    slotted.classList.add(avatarClass);
  }

  private standardizeAvatar(soulAvatar: HTMLSoulAvatarElement) {
    soulAvatar.size = this.size;
    soulAvatar.thumbnailOnly = true;
    soulAvatar.status = undefined;
  }
}

type CounterInfo = { number?: number, visible: boolean };
type AvatarClass = 'soul-avatar-group__visible-avatar' | 'soul-avatar-group__hidden-avatar';
