@use 'modules/core';

:host {
  display: block;
}

.soul-avatar-group__visible-avatar:not(:first-child),
::slotted(.soul-avatar-group__visible-avatar:not(:first-child)) {
  margin-left: var(--_soul-avatar-group-avatar-margin-left);
}

.soul-avatar-group__visible-avatar,
::slotted(.soul-avatar-group__visible-avatar) {
  @include core.flexbox-inline-row();
  vertical-align: middle;
}


:host([size='s']) {
  --_soul-avatar-group-avatar-margin-left: -#{core.$soul-space-unit-2-xs};
}

:host,
:host([size='m']) {
  --_soul-avatar-group-avatar-margin-left: -#{core.$soul-space-unit-xs};
}

:host([size='l']) {
  --_soul-avatar-group-avatar-margin-left: -#{core.$soul-space-s};
}

:host([size='xl']) {
  --_soul-avatar-group-avatar-margin-left: -#{core.$soul-space-l};
}

::slotted(.soul-avatar-group__hidden-avatar) {
  display: none;
}
